import React, {useState} from 'react';
import './welcome.css';

const Welcome = React.memo(({handleClick}) => {

	const [state, setState] = useState('welcome__entering');
	const [visited, setVisited] = useState(false);
	const [showMsg, setShowMsg] = useState(false);

	setTimeout(() => {
		if( state === 'welcome__entering') setState('welcome');
		setVisited(localStorage.getItem('visited'));
	},200);

	const startButtonClicked = (e) => {
		
		e.target.classList.add('start-button-clicked');
		setTimeout(() => {
			e.target.classList.remove('start-button-clicked');
		}, 100);
		setTimeout(() => {
			e.target.classList.add('start-button-clicked');
		}, 200);
		setTimeout(() => {
			e.target.classList.remove('start-button-clicked');
			if(!visited) {
				setState('welcome__exiting')
			} else {
				setShowMsg(true)
			}
		}, 300);
		setTimeout(() => {
			if(!visited) handleClick();
		},800)
	}

	return (
		<div className={`${state}`}>
				<div className="welcome__text">
					<h2 className="welcome__title">Pemilihan Best MT of the Month</h2>
					<h3 className="welcome__description">Brain Academy Pontianak</h3>
				</div>
				{showMsg 
		   			? <div className='error-text-wrapper'>
		   				<p className='error-text'>maaf kamu sudah pernah memilih</p>
		   			  </div> 
		   			: <button className="welcome__start-button" onClick={(e) => startButtonClicked(e)}>Mulai</button>
		   		}
		</div>
	  )
});

export default Welcome;